@import "../includes/variables";
@import "../includes/mixins";

label {
    margin-bottom: 2px;
    font-weight: 400;
}

b{
    font-weight: 400;
}

.checkbox label, .radio label{
    font-weight: 300;
}

textarea.form-control {
    height: 220px;
}

.btn {
    @include fontDefault();
    font-weight: 300;
    border-radius: $defaultBorderRadius;
    padding: 7px 15px;

    .glyphicon ~ .lbl {
        margin-left: 8px;
    }
}

.checkbox {
    margin: 0;
}

.form-control {
    @include placeholderColor($lightGray);
    border-radius: $defaultBorderRadius;
    box-shadow: none;
    padding: 7px 12px;
    height: 36px;
    border: 1px solid $lightGray;
}

.form-control:focus, .has-error .form-control:focus {
    outline: none;
    box-shadow: none;
    -webkit-box-shadow: none;
}

.form-control-feedback {
    top: 2px;
    right: 3px;
}

.form-group .alert {
    margin-top: 8px;
    margin-bottom: 0;
}

.has-feedback label ~ .form-control-feedback {
    top: 23px;
}

.form-group {
    padding-top: 1px;
    padding-bottom: 1px;
    margin-bottom: 15px;

    > .glyphicon-remove {
        top: 2px;
    }

    &.required > label:after {
        content: ' *';
    }

    &.type-checkbox {

        .checkbox {
            margin: 10px 0;
        }
    }

    &.type-checkbox ~ &.type-checkbox {

        .checkbox {
            margin: 0 0 10px 0;
        }
    }

    &:last-child{
        margin-bottom: 0;
    }
}

.type-checkbox:last-child .checkbox{
    margin-bottom: 0;
}

.tab-content {

    .table-striped tr:first-child td {
        border-top: none;
    }
}